<template>
  <div class="login-wrapper">
    <!-- <div style="position:absulute;top:0;left:0;width:7.5rem;height:100px;background:#f60;z-index:999;"></div> -->
    <div class="logo">
      <div id="ai" class="fill" :class="{'large':isLarge}"></div>
    </div>
    <transition name="fade">
      <h2 v-if="!isLarge">麓联人工智能</h2>
    </transition>
    <div class="login">
      <div class="login-bg" :style="`background-image:url(${loginBgUrl});`"></div>
      <div class="login-inner" :class="{'loaded':!isLarge}">
        <transition name="fade">
          <div class="form" v-if="!isLarge">
            <div class="form-row">
              <label>账号</label>
              <input type="text" v-model="account" />
            </div>
            <div class="form-row">
              <label>密码</label>
              <input type="password" v-model="password" @keyup.enter="toLogin" />
            </div>
            <div class="form-row-btn">
              <a
                button
                class="primary"
                :class="{'disabled':logining}"
                href="javascript:;"
                @click="toLogin"
              >{{logining?'登录中':'登录'}}</a>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import loginBgUrl from '@/assets/images/login_bg.png'
import mySvg from '@/assets/svg/AI.svg'

export default {
  name: "Login",
  data() {
    return {
      loginBgUrl:loginBgUrl,
      account: "",
      password: "",
      logining: false,
    };
  },
  computed: {
    ...mapGetters(["isLarge"])
  },
  beforeRouteEnter (to, from, next) {
    var img = new Image()
    img.onload = next
    img.src = loginBgUrl
  },
  created() {
    document.body.style.backgroundColor = "#E8EAF6";
    this.mySvg = mySvg
  },
  mounted() {
    var duration = this.isLarge?100:1
    var hi = new Vivus(
      "ai",
      {
        type: "scenario-sync",
        start: "autostart",
        dashGap: 20,
        forceRender: false,
        duration,
        file:this.mySvg,
      },
      () => {
        setTimeout(_ => {
          this.setIsLarge(false)
        }, 2000);
      }
    );
  },
  destroyed() {
    document.body.style.backgroundColor = "#fff";
  },
  methods: {
    ...mapMutations(["setIsLarge"]),
    ...mapActions(["login"]),
    checkLogin(){
      if(this.account.trim() === "" || this.password.trim() === ""){
        alert("账号或密码不正确")
        return
      }
      return true
    },
    toLogin() {
      if (this.logining) return;
      if(!this.checkLogin()) return
      this.logining = true;
      this.login({
        account: this.account,
        password: this.password
      })
        .then(res => {
          this.logining = false;
          this.$router.push("/");
        })
        .catch(e => {
          alert("账号或密码不正确")
          this.logining = false;
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.login-wrapper {
  height: 100%;
  position: relative;
  //overflow:hidden;
  h2 {
    position: absolute;
    z-index: 102;
    color: #fff;
    font-weight: normal;
    margin-left: 0.2rem;
    transform: translate3d(2.4rem, 1.4rem, 0);
  }
  .logo {
    position: absolute;
    z-index: 103;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;

    .fill {
      width: 2rem;
      height: 2.2rem;
      transform: translate3d(0.4rem, 0.4rem, 0);
      transition: all cubic-bezier(1, 0.01, 0.99, 0.47) 1.4s;
      &.large {
        width: 7.5rem;
        height: 9rem;
        transform: translate3d(0, 0.4rem, 0);
      }
    }
  }
  .login {
    position: relative;
    height: 100%;
    min-height: 10.5rem;
    z-index: 99;
    .login-bg {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      background-repeat:no-repeat;
      background-size: cover;
    }
    .login-inner {
      position: relative;
      height: 100%;
      background-color: rgba(137, 71, 155, 1);
      transition:  background-color linear 5s;
      overflow: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      &.loaded{
        background-color: rgba(137, 71, 155, 0.8);
      }
      .form {
        //padding-top: 2rem;
        width: 70%;
        .form-row {
          border-bottom: 1px solid #fff;
          margin-bottom: 1rem;
          label {
            color: #fff;
            font-weight: normal;
            font-size: 0.28rem;
          }
          input {
            border: 0 none;
            outline: 0 none;
            background: transparent;
            font-size: 0.28rem;
            color: #fff;
          }
        }
        .form-row-btn {
          padding-top: 0.2rem;
          a {
            width: 100%;
            text-align: center;
            font-size: 0.36rem;
            padding: 0.24rem 0.24rem 0.2rem 0.44rem;
            letter-spacing: 0.2rem;
          }
        }
      }
    }
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: all 2s;
  transition-delay: 1.4s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
  transform: translateY(0.6rem);
}
</style>